@import '../../constants/variable';

.header
{
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.38));
    color: #fff;
    padding: 0 34px 0 40px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    height: 48px;

    h1
    {
        font-size: 1em;
        position: absolute;
        left: 40px;
        display: flex;
        align-items: center;
        margin: 0;

        a
        {
            color: #fff;
            text-decoration: none;
        }
    }

    .small-title
    {
        font-size: .75em;
        margin-left: 2px;
        font-weight: 300;
    }


    .empty
    {
        flex: auto;
    }

    nav
    {
        display: flex;

        @include rwd(ipad)
        {
            display: none;
        }

        > div
        {
            margin-left: 32px;
            font-size: 14px;

            &:last-child
            {
                margin-right: 80px;
            }

            a
            {
                color: #ffbfd4 !important;
                cursor: pointer;
                transition: .3s;

                &:hover
                {
                    color: #fff !important;
                }
            }
        }
    }
    

    .logo
    {
        margin-right: 3px;
        display: flex;
        align-items: center;

        img
        {
            width: 48px;
            height: 48px;
        }
    }


    .language
    {
        width: 82px;
        padding: 0 8px;
        height: 24px;
        line-height: 24px;
        z-index: 100;
        cursor: pointer;
        position: relative;
        border: 1px solid #d9d9d9;
        font-size: .75em;
        color: #d9d9d9;

        > div
        {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        .lang-list
        {
            display: block;
            position: absolute;
            top: 24px;
            left: -1px;
            z-index: -99;
            opacity: 0;
            transition: .5s;
            background-color: transparent;
            widows: auto;
            pointer-events: none;
            padding-bottom: 5px;

            > div
            {
                padding: 3px 8px;
                width: 82px;
                text-align: center;
                border: 1px solid #d9d9d9;
                &:first-child
                {
                    border-top: none;
                }
                &:hover
                {
                    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.08));
                }
            }
        }
        &:hover
        {
            .lang-list
            {
                opacity: 1;
                transition: .5s;
                pointer-events: auto;
            }
        }
    }

}