@import '../../constants/variable';

div.feature
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    @include rwd(ipad)
    {
        flex-direction: column;
    }

     > div.item
    {
        width: 33.333%;
        // margin-right: 5%;
        border-radius: 8px;
        padding: 0 5%;

        @include rwd(ipad)
        {
            width: 100%;
        }

        &:last-child
        {
            margin-right: 0;
        }

         > div.img
        {
            margin: 10% 0;

             > img
            {
                display: block;

                width: 48px;
                height: 48px;
                margin: 0 auto;
            }
        }

         > div.title
        {
            font-size: 24px;
            font-weight: 500;
            font-style: italic;

            margin-bottom: 5%;

            text-align: center;
            letter-spacing: normal;

            color: #ab4af2;
        }

         > div.txt
        {
            line-height: 1.71;

            text-align: center;
            letter-spacing: .8px;

            color: #fff;
        }
    }
}
