@import '../../constants/variable';

.c_contact
{
    color: #d9d9d9;
    padding-bottom: 97px;

    h2
    {
        margin: 0;
        letter-spacing: 0.8px;
        font-size: 36px;
        text-align: center;
        margin-bottom: 40px;
    }

    input
    {
        background-color: transparent;
        border-radius: 2px;
        border: solid 1px #8a8a8a;
    }

    .data
    {
        width: 75%;
        margin: 0 auto;
        display: flex;
        margin-bottom: 23px;

        @include rwd(ipad)
        {
            flex-direction: column;
        }

        > div
        {
            margin-right: 24px;
            flex: 1;

            @include rwd(ipad)
            {
                width: 100%;
                margin-bottom: 8px;
            }

            &:last-child
            {
                margin-right: 0;
            }

            input
            {
                width: 100%;
                font-size: 12px;
                padding: 3px 8px;
                outline: none;
                color: #d9d9d9;
                height: 32px;
            }
        }

        @include rwd(ipad)
        {
            width: 90%;
        }

    }

    .message
    {
        width: 75%;
        margin: 0 auto;
        margin-bottom: 40px;

        textarea
        {
            height: 185px;
            width: 100%;
            padding: 3px 8px;
            background-color: transparent;
            border-radius: 2px;
            color: #d9d9d9;
            border: solid 1px #8a8a8a;
            outline: none;
        }

        @include rwd(ipad)
        {
            width: 90%;
        }
    }

    .submit
    {
        background-image: linear-gradient(285deg, #7166fc, #ab4af2);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        max-width: 240px;
        padding: 10px;
        margin: 0 auto;
        margin-bottom: 10px;
        color: #fff;
        border-radius: 5px;
        cursor: pointer;
    }
}