@import '../../constants/variable';

div.price
{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    @include rwd(ipad)
    {
        display: none;
    }

     > div.item
    {
        width: 15%;
        margin-right: 2%;
        padding: 1%;
        display: table;
        border-radius: 8px;
        background-color: #0e1223;
        box-shadow: 0 8px 8px 0 rgba(0, 0, 0, .16);

        &:last-child
        {
            margin-right: 0;
        }

         > div
        {
            font-size: 16px;
            font-weight: 500;
            line-height: 1;

            margin-bottom: 5%;

            text-align: left;
        }

         > div.title
        {
            color: #8a8a8a;
        }

         > div.price
        {
            color: #d9d9d9;
        }

         > div.magnitude
        {
            width: 35%;
            text-indent: 3%;
            border-radius: 5px;
            color: #ffffff;
            padding: 4px 2px;
            &.up
            {
                color: #27ca7b;
            }
            &.down
            {
                color: #f44173;
            }
        }
    }
}
