@import '../../constants/variable';

div.product
{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    @include rwd(ipad)
    {
        flex-direction: column;
    }

     > div.item
    {
        width: 30%;
        margin-right: 5%;
        border-radius: 8px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        @include rwd(ipad)
        {
            width: 100%;
            margin-bottom: 10px;
        }

        &:last-child
        {
            margin-right: 0;
        }

         > .paddingTop
        {
            position: relative;

            width: 100%;
            padding-bottom: 45%;
        }
    }
}
