@import '../../constants/variable';

.c_subscribe
{
    color: #d9d9d9;
    padding-top: 50px;
    font-family: 'Fira Mono' 'Fira Sans';

    h2
    {
        margin: 0;
        letter-spacing: 0.8px;
        font-size: 36px;
        text-align: center;

        span
        {
            position: relative;

            &::after
            {
                position: absolute;
                width: 200%;
                bottom: -16px;
                left: -50%;
                content: '';
                border-bottom: 1px solid #8a8a8a;

                @include rwd(ipad)
                {
                    width: 100%;
                    left: 0;
                }
            }
        }
    }

    .connect
    {
        display: flex;
        margin-top: 60px;
        justify-content: center;
        margin-bottom: 120px;

        @include rwd(ipad)
        {
            width: 90%;
            justify-content: space-between;
            margin: 0 auto;
            margin-top: 60px;
            margin-bottom: 120px;

        }

        > div
        {
            margin-right: 40px;

            @include rwd(ipad)
            {
                margin-right: 0;
            }

            img
            {
                cursor: pointer;
            }

            &:last-child
            {
                margin-right: 0;
            }
        }
    }
}