@import '../../constants/variable';

div.co_landingPage
{
    *
    {
        box-sizing: border-box;
    }

     > section
    {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

         > div.container
        {
            width: 90%;
            margin: 0 auto;
            padding: 3% 0;
        }

        &.getStarted
        {
            background-image: linear-gradient(118deg, #3023ae, #f44173);
            position: relative;

            perspective: 1400px;

            .box
            {
                position: absolute;
                z-index: 0;

                // transition: .1s;
                transform-origin: top top;

                &:nth-child(1)
                {
                    top: 24%;
                    right: 23%;
                    height: 10%;
                    // transform: rotate(20deg);
                }

                &:nth-child(2)
                {
                    top: 24%;
                    left: 25%;
                    height: 8%;
                    // transform: rotate(140deg);
                }

                
                &:nth-child(3)
                {
                    top: 10%;
                    left: 10%;
                    height: 20%;
                }

                &:nth-child(4)
                {
                    top: 6%;
                    left: 30%;
                    height: 10%;
                    // transform: rotate(270deg);
                }

                &:nth-child(5)
                {
                    top: 9%;
                    left: 60%;
                    height: 25%;
                }

                &:nth-child(6)
                {
                    top: 17%;
                    right: 5%;
                    height: 5%;
                    // transform: rotate(-20deg);
                }

                &:nth-child(7)
                {
                    top: 23%;
                    left: 45%;
                    height: 4%;
                    // transform: rotate(60deg);
                }

                &:nth-child(8)
                {
                    top: 45%;
                    left: 16%;
                    height: 4%;
                    // transform: rotate(-90deg);
                }

                &:nth-child(9)
                {
                    top: 40%;
                    left: 45%;
                    height: 6%;
                    // transform: rotate(40deg);
                }

                &:nth-child(10)
                {
                    top: 48%;
                    right: 10%;
                    height: 5%;
                    // transform: rotate(120deg);
                }

                &:nth-child(11)
                {
                    top: 68%;
                    left: 8%;
                    height: 8%;
                    // transform: rotate(-30deg);
                }

                &:nth-child(12)
                {
                    top: 58%;
                    left: 25%;
                    height: 10%;
                    // transform: rotate(0deg);
                }

                &:nth-child(13)
                {
                    top: 71.5%;
                    left: 60%;
                    height: 7%;
                    // transform: rotate(-120deg);
                }

                &:nth-child(14)
                {
                    top: 65%;
                    right: 20%;
                    height: 10%;
                    // transform: rotate(40deg);
                }

                &:nth-child(15)
                {
                    top: 67%;
                    right: 5%;
                    height: 8%;
                    // transform: rotate(-120deg);
                }

                &:nth-child(16)
                {
                    top: 96%;
                    left: 45%;
                    height: 12%;
                    // transform: rotate(-60deg);
                }
            }

            
            a
            {
                color: #fff;
                text-decoration: none;
            }

             > div.container > .title
            {
                font-size: 36px;
                width: 60%;
                margin: 0 auto;
                padding: 11% 0;
                text-align: center;
                text-align: center;
                letter-spacing: .8px;
                color: #fff;
                position: relative;

                display: flex;
                justify-content: center;
                align-items: center;

                h2
                {
                    position: absolute;
                    font-size: 36px;
                    font-weight: normal;
                    left: 50%;
                    transform: translate(-50%, 0);
                    width: 100%;
                    margin: 0;
                    @include rwd(ipad)
                    {
                        font-size: 20px;
                    }
                }
            }

             > div.container > .accept
            {
                display: flex;
                align-items: center;
                justify-content: center;

                width: 60%;
                margin: 0 auto;
                margin-bottom: 3%;

                span
                {
                    font-weight: bold;

                    margin-left: 20px;

                    text-align: center;
                    letter-spacing: .8px;

                    color: #fff;
                }
            }

             > div.container > .getStarted
            {
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                max-width: 240px;
                // height: 32px;
                margin: 0 auto;
                margin-bottom: 10px;

                > a
                {
                    width: 100%;
                    padding: 5%;
                }

                &.beta
                {
                    margin-bottom: 3%;
                    background-image: linear-gradient(285deg, #8866fc, #dd4af2);
                }

                > a
                {
                    width: 100%;
                    padding: 5%;
                }

                cursor: pointer;

                color: #fff;
                border-radius: 5px;
                background-image: linear-gradient(285deg, #7166fc, #ab4af2);

                p
                {
                    font-size: 13px;
                    margin: 0;
                    margin-bottom: 5px;
                }

                span 
                {
                    font-size: 18px;
                }
            }

        }

        &.product,
        &.feature
        {
            background-color: #000;
        }

        &.feature
        {
             > div.container > .title
            {
                font-size: 36px;

                text-align: center;
                letter-spacing: .8px;

                color: #f44173;
            }
        }
    }

    .bg-holder
    {
        background-image: url('/img/bg-contact@2x.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        footer
        {
            color: #999;
            width: 95%;
            margin: 0 auto;
            padding-bottom: 13px;
            background-color: transparent;
            height: auto;
            display: block;
            font-size: 14px;
            position: relative;
            bottom: auto;
            span
            {
                margin-right: 32px;
            }

            p
            {
                display: flex;
                align-items: center;
        
                span
                {
                    font-size: 16px;
                    margin: 4px 5px;
                    margin-bottom: 0;
                    font-weight: lighter;
                }
            }
        }
    }
}
